html, body, .pageContainer  {

    margin: 0px;
    padding: 0px;
}

body {
    min-height: 100vh;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: lightgray !important;
}

#mapContainer {
    height: 60vh;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.23);
}

.html {
    background-color: black;
    
}

.flexcontainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1em;
}

.flexcontainercolumn {
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1em;
}

.footer {
    bottom: 0;
    right: 0;
}
  




